import React from 'react'
import { Nav, NavbarBrand } from 'react-bootstrap'
import { LinkContainer } from 'react-router-bootstrap'


function CheckoutSteps({ step1, step2, step3, step4 }) {




    return (
        <Nav className='justify-content-center  text-capitalize mb-2  '>

            <Nav.Item>
                {step1 ? (
                    <LinkContainer to='/login'>
                        <Nav.Link className='h6'>Login</Nav.Link>
                    </LinkContainer>
                ) : (
                    <Nav.Link disabled className='h6'>Login</Nav.Link>
                )}
            </Nav.Item>

            <Nav.Item>
                {step2 ? (
                    <LinkContainer to='/shipping'>
                        <Nav.Link className='h6'>Shipping</Nav.Link>
                    </LinkContainer>
                ) : (
                    <Nav.Link disabled className='h6'>Shipping</Nav.Link>

                )}
            </Nav.Item>

            <Nav.Item>
                {step3 ? (
                    <LinkContainer to='/payment'>
                        <Nav.Link className='h6'>Payment</Nav.Link>
                    </LinkContainer>
                ) : (
                    <Nav.Link disabled className='h6'>Payment</Nav.Link>

                )}
            </Nav.Item>

            <Nav.Item>
                {step4 ? (
                    <LinkContainer to='/placeorder'>
                        <Nav.Link className='h6'>Place Order</Nav.Link>
                    </LinkContainer>
                ) : (
                    <Nav.Link disabled className='h6'>Place Order</Nav.Link>

                )}
            </Nav.Item>

        </Nav>
    )
}

export default CheckoutSteps