import React, { useState, useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Link } from 'react-router-dom'
import { Row, Col, Image, ListGroup, Button, Card, Form } from 'react-bootstrap'
import Rating from '../components/Rating'
import Loader from '../components/Loader'
import Message from '../components/Message'
import { createProductReview } from '../redux/productActions'
import { PRODUCT_CREATE_REVIEW_RESET } from '../constants/productConstants'

import { listProductDetails } from '../redux/productActions'




function ProductScreen({ match, history }) {
  // const product = products.find((p) => p._id === match.params.id)
  // const [product, setProduct] = useState([])

  const [qty, setQty] = useState(1)
  const [rating, setRating] = useState(0)
  const [comment, setComment] = useState('')


  const dispatch = useDispatch()
  const productDetails = useSelector(state => state.productDetails)
  const { loading, error, product } = productDetails

  const userLogin = useSelector(state => state.userLogin)
  const { userInfo } = userLogin


  const productReviewCreate = useSelector(state => state.productReviewCreate)
  const {
    loading: loadingProductReview,
    error: errorProductReview,
    success: successProductReview,
  } = productReviewCreate

  useEffect(() => {
    // async function fetchProduct() {
    //   const { data } = await axios.get(`api/products/${match.params.id}`)
    //   setProduct(data)
    // }
    // fetchProduct()
    if (successProductReview) {
      setRating(0)
      setComment('')
      dispatch({ type: PRODUCT_CREATE_REVIEW_RESET })
    }
    dispatch(listProductDetails(match.params.id))

  }, [dispatch, match])

  const addToCartHandler = () => {
    history.push(`/cart/${match.params.id}?qty=${qty}`)
  }

  const submitHandler = (e) => {
    e.preventDefault()
    dispatch(createProductReview(
      match.params.id, {
      rating,
      comment
    }
    ))
  }

  return (
    <div>
      <Link to='/' className='btn btn-light my-3'>&#8592;Go Back</Link>
      {

        loading ? <Loader />
          : error
            ? <Message variant='danger'>{error}</Message>
            : (
              <div>
                <Row>
                  <Col md={6}>
                    <Image src={product.image} alt={product.name} fluid />
                  </Col>

                  <Col md={3}>

                    <ListGroup variant="flush">

                      <ListGroup.Item>
                        <h3>{product.name}</h3>
                      </ListGroup.Item>

                      <ListGroup.Item>
                        <small><Rating value={product.rating} text={`${product.numReviews} reviews`} color={'#f8e825'} /></small>
                      </ListGroup.Item>

                      <ListGroup.Item>
                        <small><h3>Price: ${product.price}</h3></small>
                      </ListGroup.Item>

                      <ListGroup.Item>
                        <h4>Description: {product.description}</h4>
                      </ListGroup.Item>
                    </ListGroup>
                  </Col>

                  <Col md={3}>
                    <Card>
                      <ListGroup cariant="flush">
                        <ListGroup.Item>
                          <Row>
                            <Col><small>Price:</small></Col>
                            <Col>
                              <small>${product.price}</small>
                            </Col>
                          </Row>
                        </ListGroup.Item>

                        <ListGroup.Item>
                          <Row>
                            <Col><small>Status:</small></Col>
                            <Col>
                              <small>
                                {product.countInStock > 0 ? 'In Stock' : 'Out of Stock'}
                              </small>
                            </Col>
                          </Row>
                        </ListGroup.Item>
                        {product.countInStock > 0 && (
                          <ListGroup.Item>
                            <Row>
                              <Col><small>Qty:</small></Col>
                              <Col cs='auto' className='my-1'>
                                <Form.Control as="select" value={qty} onChange={(e) => setQty(e.target.value)}>
                                  {
                                    [...Array(product.countInStock).keys()].map((x) => (
                                      <option key={x + 1} value={x + 1}>
                                        {x + 1}
                                      </option>
                                    ))
                                  }

                                </Form.Control>
                              </Col>
                            </Row>
                          </ListGroup.Item>
                        )}
                        <ListGroup>
                          <Button onClick={addToCartHandler} className='btn-block' disabled={product.countInStock === 0} type='button'>
                            Add to cart
                          </Button>
                        </ListGroup>
                      </ListGroup>
                    </Card>
                  </Col>

                </Row>

                <Row>
                  <Col md={6} className='py-5'>
                    <h4>Reviews</h4>
                    {product.reviews.length === 0 && <Message variant='info'>No Reviews</Message>}
                    <ListGroup variant='flush' className='h4'>
                      {product.reviews.map((review) => (
                        <ListGroup.Item key={review._id} className='text-capitalize'>
                          {review.name}
                          <Rating value={review.rating} color='#f8e825' />
                          {review.createdAt.substring(0, 10)}
                          <p>{review.comment}</p>
                        </ListGroup.Item>
                      ))}

                      <ListGroup.Item className='h5'>
                        <h4 className='py-3'>Write a review</h4>

                        {loadingProductReview && <Loader />}
                        {successProductReview && <Message variant='success'>Review sucessfully submitted!</Message>}
                        {errorProductReview && <Message variant='danger'>{errorProductReview}</Message>}

                        {/* If userinfo let them use form, otherwise redirect to login */}
                        {userInfo ? (
                          <Form onSubmit={submitHandler}>
                            <Form.Group controlId='rating' className='py-3'>
                              <Form.Label>Rating</Form.Label>
                              <Form.Control
                                className='form-select'
                                as='select'
                                value={rating}
                                onChange={(e) => setRating(e.target.value)}>
                                <option value=' '>Select...</option>
                                <option value='1'>1 - Poor</option>
                                <option value='2'>2 - Fair</option>
                                <option value='3'>3 - Good</option>
                                <option value='4'>4 - Great</option>
                                <option value='5'>5 - Excellent</option>
                              </Form.Control>
                            </Form.Group>

                            <Form.Group controlId='comment'>
                              <Form.Label>Review</Form.Label>
                              <Form.Control
                                as='textarea'
                                row='5'
                                value={comment}
                                onChange={(e) => setComment(e.target.value)}
                              ></Form.Control>
                            </Form.Group>

                            <Button
                              disabled={loadingProductReview}
                              type='submit'
                              variant='primary'>
                              Submit
                            </Button>

                          </Form>

                        ) : (
                          <Message variant='info'>Please <Link to='/login'>login</Link> to write a review</Message>
                        )}
                      </ListGroup.Item>
                    </ListGroup>
                  </Col>
                </Row >
              </div >
            )
      }



    </div >
  )
}

export default ProductScreen